/* Base utilities shared across FA/EN builds.
   Keep these selectors as the single source of truth
   so the design system does not override them. */

body {
  margin: 0;
  font-family: var(--font-body, sans-serif);
}

/* --- Toast --- */

.dynamic-toast {
  position: fixed;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%) translateY(100%);
  opacity: 0;
  background: var(--toast-bg, rgba(0, 0, 0, 0.8));
  color: var(--toast-color, #fff);
  padding: 0.5rem 1rem;
  border-radius: 4px;
  z-index: 1000;
  font-size: 0.875rem;
  line-height: 1.3;
  max-width: min(90vw, 420px);
  text-align: center;
  box-shadow: 0 8px 24px var(--shadow-color-light, rgba(0, 0, 0, 0.3));
  transition:
    transform var(--transition-duration-medium, 200ms)
      var(--transition-easing-standard, ease),
    opacity var(--transition-duration-medium, 200ms)
      var(--transition-easing-standard, ease);
}

.dynamic-toast.show {
  transform: translateX(-50%) translateY(0);
  opacity: 1;
}

/* --- Themes --- */

.dark-mode {
  background: #0f172a;
  color: #fff;
  --primary-color: #61dafb;
  --primary-rgb: 97, 218, 251;
  --secondary-color: #f0f0f0;
  --accent-color: #5ab0f5;
  --cta-text-color: #000;
  --highlight-color: #ffca28;
  --highlight-rgb: 253, 216, 53;
  --highlight-darker: #c7a700;
  --background-start-color: #1f2631;
  --background-end-color: #151a24;
  --container-bg: #1e1e1e;
  --card-bg: #242424;
  --navbar-bg: rgba(30, 30, 30, 0.95);
  --giscus-bg: #1e1e1e;
  --skills-bg: #3a3a3a;
  --toast-bg: var(--primary-color);
  --toast-color: #fff;
  --shadow-color-light: rgba(0, 0, 0, 0.15);
  --shadow-color-medium: rgba(0, 0, 0, 0.3);
  --shadow-color-strong: rgba(0, 0, 0, 0.45);
  --text-color: #d0d0d0;
  --text-color-light: #a9a9a9;
  --text-color-link: var(--primary-color);
  --heading-color: var(--secondary-color);
  --tagline-color: #bcbcbc;
  --footer-color: #a0a0a0;
  --lang-switcher-active-color: #c0c0c0;
  --border-color: #3a3a3a;
  --timeline-line-color: #555555;
  --timeline-dot-border-color: var(--primary-color);
  --timeline-content-border-color: #4a4a4a;
  --giscus-border: 1px solid var(--border-color);
  --faq-link-hover-color: var(--primary-color);
  --card-link-hover-text-color: #000;
}

.light-mode {
  background: #ffffff;
  color: #0f172a;
}

/* Footer links */

.footer-links {
  margin-top: 1rem;
  text-align: center;
}

.footer-link {
  color: #bbb;
  font-size: 0.9rem;
  text-decoration: none;
  padding: var(--space-1, 0.25rem) var(--space-2, 0.5rem);
  border-radius: 6px;
  transition:
    color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    background-color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease);
  will-change: color, background-color;
}

.footer-link:hover,
.footer-link:focus-visible {
  background-color: rgba(100, 100, 100, 0.1);
  color: var(--faq-link-hover-color, #2563eb);
  outline: none;
}

/* Micro profile */

.micro-profile {
  font-size: 0.75rem;
  color: #555;
  opacity: 0.6;
  text-align: center;
  max-width: 400px;
  margin: 2rem auto 0;
  padding: 0.5rem 1rem;
  border-top: 1px dashed #ccc;
  will-change: opacity;
}

.micro-profile img.u-photo {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 50%;
  margin: 0.5rem auto;
  display: block;
}

.micro-profile a.u-url,
.micro-profile a.u-email {
  display: inline-block;
  color: #0066cc;
  font-weight: bold;
  text-decoration: none;
}

.micro-profile a.u-url:hover,
.micro-profile a.u-email:hover {
  text-decoration: underline;
}

/* Theme switch */

.theme-switch {
  position: relative;
  display: inline-flex;
  align-items: center;
  width: 48px;
  height: 24px;
  margin: 0 var(--space-md, 1rem);
  vertical-align: middle;
}

.theme-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  inset: 0;
  background-color: var(--text-color-light, #9ca3af);
  transition: background-color var(--transition-duration-short, 120ms)
    var(--transition-easing-standard, ease);
  border-radius: 24px;
}

.slider::before {
  position: absolute;
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: var(--card-bg, #ffffff);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;

  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  content: "\f185";
  color: var(--primary-color, #0f172a);
  font-size: 0.8em;
  transition:
    transform var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    background-color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease);
}

input:checked + .slider {
  background-color: var(--primary-color, #2563eb);
}

input:focus-visible + .slider {
  box-shadow: 0 0 0 3px var(--accent-color, #2563eb),
    0 0 0 6px var(--shadow-color-light, rgba(0, 0, 0, 0.18));
}

input:checked + .slider::before {
  transform: translateX(24px);
  content: "\f186";
  color: #ffffff;
}

/* Utility visibility */

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* Utility layout */

.min-h-screen {
  min-height: 100vh;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.text-center {
  text-align: center;
}

.p-8 {
  padding: 2rem;
}

.text-lg {
  font-size: 1.125rem;
}

.text-4xl {
  font-size: 2.25rem;
}

.font-bold {
  font-weight: 700;
}

.mb-4 {
  margin-bottom: 1rem;
}

.mb-6 {
  margin-bottom: 1.5rem;
}

/* Global focus states */

a:focus-visible,
button:focus-visible,
input:focus-visible,
select:focus-visible,
textarea:focus-visible {
  outline: 3px solid var(--accent-color, #2563eb);
  outline-offset: var(--space-xs, 0.15rem);
  box-shadow: 0 0 0 4px var(--shadow-color-light, rgba(0, 0, 0, 0.15));
  z-index: 10;
  position: relative;
}

a:focus:not(:focus-visible),
button:focus:not(:focus-visible),
input:focus:not(:focus-visible),
select:focus:not(:focus-visible),
textarea:focus:not(:focus-visible) {
  outline: none;
}

/* Press card styles */

.press-card {
  display: flex;
  align-items: center;
  gap: var(--space-md, 1rem);
  padding: var(--space-md, 1rem);
  margin-bottom: var(--space-md, 1rem);
  background-color: var(--card-bg, #ffffff);
  border: 1px solid var(--border-color, #e5e7eb);
  border-radius: var(--border-radius-lg, 1rem);
  transition:
    box-shadow var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    transform var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease);
}

.press-card:hover {
  box-shadow: 0 4px 12px var(--shadow-color-medium, rgba(0, 0, 0, 0.16));
  transform: translateY(-1px);
}

.press-card img {
  width: 48px;
  height: 48px;
  flex-shrink: 0;
}

/* Download button transitions */

.download-button {
  transition:
    background-color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    transform var(--transition-duration-short, 120ms)
      var(--transition-easing-springy, cubic-bezier(0.18, 0.89, 0.32, 1.28));
}

.download-button:hover,
.download-button:focus-visible {
  background-color: var(--primary-color, #2563eb);
  transform: translateY(-2px);
  text-decoration: none;
  color: #fff;
}

/* Offline info */

.offline-info {
  display: none;
  padding: 0.5rem 1rem;
  background-color: #fffbea;
  color: #854d0e;
  border: 1px solid #fadcb3;
  border-radius: 10px;
  margin-top: 0.5rem;
  text-align: center;
  font-size: 0.875rem;
}

body.offline .offline-info {
  display: block;
}

/* --- Disable AOS hiding when animations are off --- */

.aos-disabled [data-aos] {
  opacity: 1 !important;
  transform: none !important;
  transition: none !important;
}
